import { Support } from 'components';
import { useEffect, useState } from 'react';
import { Map, MapMarker } from 'react-kakao-maps-sdk';

const MapPage = () => {
  const [state, setState] = useState({
    center: {
      lat: 33.450701,
      lng: 126.570667
    },
    errMsg: null,
    isLoading: true
  });
  const [support, setSupport] = useState({
    geolocation: false,
    gyroscope: false
  });

  useEffect(() => {
    if ('geolocation' in navigator) {
      // GeoLocation    
      setSupport((prev) => ({
        ...prev,
        geolocation: true
      }));
      navigator.geolocation.getCurrentPosition(
        (position) => {
          setState((prev) => ({
            ...prev,
            center: {
              lat: position.coords.latitude, // 
              lng: position.coords.longitude // 
            },
            isLoading: false
          }));
        },
        (err) => {
          setState((prev) => ({
            ...prev,
            errMsg: err.message,
            isLoading: false
          }));
        }
      );
    } else {
      // HTML5 GeoLocation         
      setState((prev) => ({
        ...prev,
        errMsg: 'geolocation  ..',
        isLoading: false
      }));
    }
  }, []);

  return (
    <div className="App flex h-full flex-col gap-4">
      <Support support={support.geolocation} />
      <button
        className="primary block w-full"
        onClick={() => {
          if ('geolocation' in navigator) {
            setState((prev) => ({
              ...prev,
              isLoading: true
            }));
            // GeoLocation    
            navigator.geolocation.getCurrentPosition(
              (position) => {
                setState((prev) => ({
                  ...prev,
                  center: {
                    lat: position.coords.latitude, // 
                    lng: position.coords.longitude // 
                  },
                  isLoading: false
                }));
              },
              (err) => {
                setState((prev) => ({
                  ...prev,
                  errMsg: err.message,
                  isLoading: false
                }));
              }
            );
          } else {
            // HTML5 GeoLocation         
            setState((prev) => ({
              ...prev,
              errMsg: 'geolocation  ..',
              isLoading: false
            }));
          }
        }}
      >
          
      </button>
      {state.isLoading && <div>  ...</div>}
      <div className="flex-grow">
        <Map
          center={state.center}
          level={3}
          style={{
            width: '100vw - 1rem',
            height: '100%'
          }}
        >
          {!state.isLoading && (
            <MapMarker position={state.center}>
              <div style={{ padding: '5px', color: '#000' }}>
                {state.errMsg ? state.errMsg : ' ?!'}
              </div>
            </MapMarker>
          )}
        </Map>
      </div>
    </div>
  );
};

export default MapPage;
